* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.inputs {
  display: flex;
  justify-content: center;
  margin-top: 10%;
}

.divinp {
  margin: 20px;
}

input {
  border: none;
  padding: 10px;
  border: 1px solid #ccc;
}

.div-button {
  display: flex;
  justify-content: center;
}

.btn {
  border: none;
  padding: 10px;
  width: 20%;
  background-color: black;
  color: white;
  &:hover {
    cursor: pointer;
  }
}

.div-button-one {
  padding: 10px;
}

.new {
  margin: 10px;
  padding: 1%;
  width: 30%;
  flex-wrap: wrap;
   border: 1px solid #ccc;
  flex-wrap: wrap;


}

.big {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.newimg {
  width: 100%;
  height: auto;
}

.pnew {
  color: rgb(0, 0, 0);
  margin: 10px;
}
.pnew2 {
  margin-top: 15px;
}

.basketbtn {
  border: none;
  background-color: black;
  color: white;
  padding: 10px;
  transition: 0.5s;
  margin: 10px;

  &:hover {
    cursor: pointer;
    background-color: white;
    color: black;
  }
}

//
.bookmarkBtn {
  width: 100px;
  height: 40px;
  border-radius: 40px;
  border: 1px solid rgba(255, 255, 255, 0.349);
  background-color: rgb(12, 12, 12);
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition-duration: 0.3s;
  overflow: hidden;
}

.IconContainer {
  width: 30px;
  height: 30px;
  background: linear-gradient(to bottom, rgb(255, 136, 255), rgb(172, 70, 255));
  border-radius: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  z-index: 2;
  transition-duration: 0.3s;
}

.icon {
  border-radius: 1px;
}

.text {
  height: 100%;
  width: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  z-index: 1;
  transition-duration: 0.3s;
  font-size: 1.04em;
}

.bookmarkBtn:hover .IconContainer {
  width: 90px;
  transition-duration: 0.3s;
}

.bookmarkBtn:hover .text {
  transform: translate(10px);
  width: 0;
  font-size: 0;
  transition-duration: 0.3s;
}

.bookmarkBtn:active {
  transform: scale(0.95);
  transition-duration: 0.3s;
}


@media only screen and (max-width: 600px) {
       .new{
        padding: 20px;
        width: 100%;
   
  
       }
       .newimg{
          width: 80%;
          height: 100%;
          text-align: center;
          margin-left: 10%;
       }
       .pnew{
  font-size: 10px;

       }

}